@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;1,300;1,400&family=Roboto:ital,wght@0,300;1,300&display=swap);
:root {
	--borderRadius: 7px;
}
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
body {
	margin: 0;
	font-family: 'Montserrat', sans-serif;
}

code {
	font-family: 'Montserrat', sans-serif;
}

.header {
	position: relative;
	width: 100%;
	height: 80px;
	padding: 0 35px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	box-shadow: 0 0 20px #0001;
}
.header .box {
	position: relative;
	height: 100%;
	width: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
}
.header .box ul {
	height: 100%;
	width: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
	list-style: none;
}
.header .box ul li {
	padding: 15px 0px;
	width: 90px;
	text-align: center;
	position: relative;
	margin: 0 10px;
	transition: 0.5s;
}
.header .box ul li:hover {
	background: rgba(0, 0, 0, 0.027);
}
.header .box ul li a {
	cursor: pointer;
	text-decoration: none;
	color: #000;
}
.header .box img {
	position: relative;
	height: 35px;
}
.header .box .handburger {
	display: none;
}
@media screen and (max-width: 1000px) {
	.header .box ul {
		display: none;
	}
	.header .box .handburger {
		display: block;
		font-size: 26px;
		cursor: pointer;
	}
}
@media screen and (max-width: 600px) {
	.header {
		padding: 0;
		padding: 0 5%;
	}
}

.canvas {
	position: relative;
	width: 80%;
	height: 350px;
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	margin: 20px auto 0;
}
.canvas .box {
	position: relative;
	width: 45%;
	height: 100%;
	overflow: hidden;
	background-color: rgba(0, 0, 0, 0.04);
	/* padding: 30px 0; */
	display: block;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: var(--borderRadius);
}
.canvas .box img {
	position: relative;
	height: 80%;
	margin: 0;
	display: block;
	max-width: 90%;
	margin: 0 auto;
}
#root > div > div.mainComponent > div.canvas > div:nth-child(3) > div,
#root > div > div.mainComponent > div.canvas > div:nth-child(3) > div > div {
	position: relative;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
#root > div > div.mainComponent > div.canvas > div:nth-child(3) > div svg {
	position: relative;
	height: 80%;
	width: 90%;
	border-radius: var(--borderRadius);
}
#root > div > div.mainComponent > div.canvas > div:nth-child(3) > div img.loader {
	position: relative;
	width: 50px;
	height: 50px;
	-webkit-animation: example 4s infinite;
	        animation: example 4s infinite;
	margin: 50px 0 20px;
}
a {
	opacity: 1;
}
a.hide {
	opacity: 0;
}

@-webkit-keyframes example {
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes example {
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
.canvas .reload {
	position: relative;
	width: 10%;
	display: block;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.canvas .reload i {
	font-size: 24px;
	cursor: pointer;
	transition: 0.5s;
	color: #808080;
}
.canvas .reload i:hover {
	-webkit-transform: scale(1.5);
	        transform: scale(1.5);
	text-shadow: 0 0 10px #0002;
	color: #000;
}
.canvas .reload p {
	position: relative;
	width: 100%;
	/* margin-left: 30%; */
	margin-top: 20px;
	/* text-align: center; */
	padding: 10px 10px;
	z-index: 10;
	/* background: #fff; */
	/* box-shadow: 0 0 10px #0004; */
	opacity: 0;
	font-size: 12px;
	cursor: none;
	pointer-events: none;
	transition: 0.75s;
}
.canvas .reload i:hover ~ p {
	opacity: 1;
}
#forMobileView {
	display: none;
}
/* Make this component responsive */

@media screen and (max-width: 1250px) {
	.canvas {
		width: 90%;
	}
}
@media screen and (max-width: 740px) {
	.canvas {
		flex-direction: column;
		/* min-height: 400px; */
		justify-content: flex-start;
	}
	.canvas > div.box {
		position: relative;
		width: 100%;
		height: 270px;
		margin-bottom: 10px;
	}
	div.canvas > div:nth-child(3) {
		display: none;
	}
	.canvas .reload {
		position: relative;
		width: 100%;
		height: 70px;
		background: #0001;
	}
	.canvas .box.haveImageToShow img {
		display: none;
	}
	.canvas .box.haveImageToShow #forMobileView,
	.canvas .box.haveImageToShow #forMobileView div.div,
	.canvas .box.haveImageToShow #forMobileView div svg {
		position: relative;
		width: 100%;
		height: 100%;
		display: block;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.canvas .box.haveImageToShow #forMobileView div svg {
		height: 80%;
		width: 90%;
	}
	.canvas .box.haveImageToShow #forMobileView {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.canvas .reload p {
		display: none;
	}
}

