:root {
	--gray: #2e65bf;
	--darkgray: #1bb0ce;
}

select {
	/* Reset Select */
	-webkit-appearance: none;
	        appearance: none;
	outline: 0;
	border: 0;
	box-shadow: none;
	/* Personalize */
	flex: 1 1;
	padding: 0 1em;
	color: #fff;
	background-color: #1bb0ce;
	background-color: var(--darkgray);
	background-image: none;
	cursor: pointer;
	font-family: 'Montserrat', sans-serif;
	opacity: 0;
	display: none;
}
/* Remove IE arrow */
select::-ms-expand {
	display: none;
}
/* Custom Select wrapper */
.select {
	position: relative;
	display: flex;
	width: 80%;
	height: 3em;
	border-radius: .25em;
	overflow: hidden;
	margin: 0 auto;
	opacity: 0;
	display: none;
}
/* Arrow */
.select::after {
	content: '\25BC';
	position: absolute;
	top: 0;
	right: 0;
	padding: 1em;
	background-color: rgb(29, 132, 153);
	transition: .25s all ease;
	pointer-events: none;
	opacity: 0;
	display: none;
}
/* Transition */
.select:hover::after {
	color: rgb(0, 66, 97);
}

